@import url('https://fonts.googleapis.com/css2?family=Chakra+Petch:wght@400;600;700&family=Orbitron:wght@400;500;600;700;800;900&family=Rajdhani:wght@400;500;600;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Exo+2:wght@300;400;600&display=swap');

.ranking-page {
    min-height: 100vh;
    background: linear-gradient(135deg, #050a18 0%, #0a1931 100%);
    padding: 20px;
    position: relative;
    overflow: hidden;
}

/* Animated dots */
.ranking-page::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: 
        radial-gradient(circle, #00ffff 1px, transparent 1px),
        radial-gradient(circle, #ff69b4 1px, transparent 1px),
        radial-gradient(circle, #4dff4d 1px, transparent 1px);
    background-size: 
        50px 50px,
        40px 40px,
        60px 60px;
    animation: moveDots 20s linear infinite;
    opacity: 0.15;
    z-index: 0;
}

@keyframes moveDots {
    0% {
        background-position: 
            0 0,
            0 0,
            0 0;
    }
    100% {
        background-position: 
            100px 100px,
            -100px 100px,
            100px -100px;
    }
}

/* Ensure content stays above dots */
.ranking-page > * {
    position: relative;
    z-index: 1;
}

/* Reset any inherited styles */
.ranking-page * {
    box-sizing: border-box;
}

.ranking-page .header {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    padding: 20px 0;
    position: relative;
    z-index: 1;
}

.ranking-page .logo1 {
    font-family: 'Russo One', sans-serif;
    font-size: 3rem;
    color: white;
    text-transform: uppercase;
    letter-spacing: 2px;
    margin: 0;
    text-align: center;
    background: linear-gradient(to bottom, #00ffff 0%, #00bfff 50%, #1e90ff 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    text-shadow: 
        3px 3px 0 #d9d9d9,
        -1px -1px 0 #d9d9d9,  
        1px -1px 0 #d9d9d9,
        -1px 1px 0 #d9d9d9,
        1px 1px 0 #d9d9d9;
    position: relative;
    padding: 10px 0;
    animation: glow 2s ease-in-out infinite alternate;
}

@keyframes glow {
    from {
        text-shadow: 
            3px 3px 0 #000,
            -1px -1px 0 #000,  
            1px -1px 0 #000,
            -1px 1px 0 #000,
            1px 1px 0 #000,
            0 0 5px #00ffff,
            0 0 10px #00ffff;
    }
    to {
        text-shadow: 
            3px 3px 0 #000,
            -1px -1px 0 #000,  
            1px -1px 0 #000,
            -1px 1px 0 #000,
            1px 1px 0 #000,
            0 0 10px #00ffff,
            0 0 20px #00ffff,
            0 0 30px #00ffff;
    }
}

.ranking-page .logo1::after {
    content: '';
    position: absolute;
    bottom: -5px;
    left: 50%;
    transform: translateX(-50%);
    width: 70%;
    height: 3px;
    background: linear-gradient(90deg, 
        rgba(0,255,255,0) 0%,
        rgba(0,255,255,0.8) 50%,
        rgba(0,255,255,0) 100%
    );
    border-radius: 2px;
    box-shadow: 0 0 10px #00ffff;
}

