.ranking-card {
    position: relative;
    padding: 30px;
    margin: 15px auto;
    background: linear-gradient(
        135deg,
        rgba(6, 20, 45, 0.4) 0%,
        rgba(8, 28, 63, 0.4) 100%
    );
    backdrop-filter: blur(12px);
    -webkit-backdrop-filter: blur(12px);
    border-radius: 15px;
    border: 2px solid rgba(0, 195, 255, 0.15);
    box-shadow: 
        0 4px 20px rgba(0, 0, 0, 0.2),
        inset 0 0 30px rgba(0, 195, 255, 0.03);
    transition: all 0.3s ease;
    overflow: hidden;
    max-width: 500px;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    cursor: pointer;
}

.ranking-card::after {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(
        90deg,
        transparent,
        rgba(0, 255, 255, 0.1),
        transparent
    );
    transition: 0.5s;
}

.ranking-card:hover::after {
    left: 100%;
}

.ranking-card:hover {
    transform: translateY(-2px);
    background: linear-gradient(
        135deg,
        rgba(8, 28, 63, 0.5) 0%,
        rgba(12, 38, 85, 0.5) 100%
