.member-image-container {
    position: relative;
    width: 160px;
    height: 160px;
    margin: 10px 0 10px;
    padding: 5px;
    border-radius: 20px;
    background: linear-gradient(
        45deg,
        rgba(0, 195, 255, 0.2),
        rgba(0, 195, 255, 0.1),
        rgba(0, 195, 255, 0.2)
    );
}

.member-image {
    width: 150px;
    height: 150px;
    border-radius: 15px;
    object-fit: cover;
    transition: all 0.3s ease;
    position: relative;
    z-index: 2;
    border: 3px solid rgba(0, 195, 255, 0.3);
    box-shadow: 
        0 0 20px rgba(0, 195, 255, 0.2),
        inset 0 0 20px rgba(0, 195, 255, 0.1);
}

.member-image-container::before {
    content: '';
    position: absolute;
    top: -2px;
    left: -2px;
    right: -2px;
    bottom: -2px;
    background: linear-gradient(
        45deg,
        rgba(0, 195, 255, 0.5),
        rgba(0, 195, 255, 0.2),
        rgba(0, 195, 255, 0.5)
    );
    border-radius: 20px;
    z-index: 1;
    animation: borderGlow 3s ease-in-out infinite;
}

.member-image-container::after {
    content: '';
    position: absolute;
    inset: -5px;
    background: linear-gradient(
        45deg,
        rgba(0, 195, 255, 0.15),
        transparent,
        rgba(0, 195, 255, 0.15)
    );
    border-radius: 25px;
    z-index: 0;
    filter: blur(5px);
}

@keyframes borderGlow {
    0%, 100% {
        opacity: 0.5;
    }
    50% {
        opacity: 1;
    }
}

