/* Background dots styling */
.background-dots {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    pointer-events: none;
    z-index: 0;
}

.dot {
    position: absolute;
    width: 6px;
    height: 6px;
    border-radius: 50%;
    filter: blur(1px);
    animation: floatDot 20s infinite ease-in-out;
    opacity: 0.5;
}

@keyframes floatDot {
    0%, 100% {
        transform: translate(0, 0) rotate(0deg);
    }
    25% {
        transform: translate(100px, 50px) rotate(90deg);
    }
    50% {
        transform: translate(50px, 100px) rotate(180deg);
    }
    75% {
        transform: translate(-50px, 50px) rotate(270deg);
    }
}

/* Generate random positions and colors for dots */
.dot-0 { background: #5DA7FF; top: 10%; left: 10%; animation-delay: -1s; }
