* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

:root {
    --container-wth: 80rem;
}

.background {
    width: 100%;
    min-height: 100vh;
    background-image: url(../imgs/mike-erskine-Xtnt5xtK03E-unsplash.jpg);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
}

.background::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(204, 245, 252, 0.24);
    z-index: 1;
}

header, section {
    position: relative;
    z-index: 2;
}

.container {
    width: 90%;
    max-width: var(--container-wth);
    margin: 0 auto;
    padding: 1rem;
    position: relative;
}

header {
    width: 100%;
    position: relative;
}

.nav {
    height: 4rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 1rem;
}

.logo {
    height: 4rem;
    width: 40px;
    background-image: url(../imgs/logo.png);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

section {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: calc(100vh - 8rem);
    padding: 1rem;
}

.form {
    width: 95%;
    max-width: 400px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    padding: 1.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 8px;
    margin: 0 auto;
}

.inputs {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
    max-width: 300px;
    margin: 0 auto;
}

.form-control {
    position: relative;
    margin: 15px 0;
}

.form-control input {
    background: none;
    color: #fff;
    font-size: 18px;
    padding: 10px 10px 10px 5px;
    display: block;
    width: 320px;
    border: none;
    border-radius: 0;
    border-bottom: 1px solid #fff;
}

.form-control input:focus {
    outline: none;
}

/* Prevent autofill background while keeping original design */
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
    -webkit-box-shadow: 0 0 0 30px transparent inset !important;
    -webkit-text-fill-color: #fff !important;
    transition: background-color 5000s ease-in-out 0s;
    background: none !important;
}

/* Keep other existing styles */
.form-control input::placeholder {
    color: rgba(255, 255, 255, 0.7);
}

.form-control input:focus::placeholder {
    color: rgba(255, 255, 255, 0.4);
}

.login-btn {
    align-items: center;
    appearance: none;
    background-image: radial-gradient(100% 100% at 100% 0, #5adaff 0, #5468ff 100%);
    border: 0;
    border-radius: 6px;
    box-shadow: rgba(45, 35, 66, .4) 0 2px 4px,rgba(45, 35, 66, .3) 0 7px 13px -3px,rgba(58, 65, 111, .5) 0 -3px 0 inset;
    box-sizing: border-box;
    color: #fff;
    cursor: pointer;
    display: inline-flex;
    font-family: "JetBrains Mono",monospace;
    height: 44px;
    justify-content: center;
    line-height: 1;
    list-style: none;
    overflow: hidden;
    padding: 0 2rem;
    position: relative;
    text-align: center;
    text-decoration: none;
    transition: box-shadow .15s,transform .15s;
    user-select: none;
    touch-action: manipulation;
    white-space: nowrap;
    font-size: 16px;
    width: 100%;
    max-width: 200px;
    margin: 1.5rem auto 0;
}

.login-btn:focus {
    box-shadow: #3c4fe0 0 0 0 1.5px inset, rgba(45, 35, 66, .4) 0 2px 4px, rgba(45, 35, 66, .3) 0 7px 13px -3px, #3c4fe0 0 -3px 0 inset;
}

.login-btn:hover {
    box-shadow: rgba(45, 35, 66, .4) 0 4px 8px, rgba(45, 35, 66, .3) 0 7px 13px -3px, #3c4fe0 0 -3px 0 inset;
    transform: translateY(-2px);
}

.login-btn:active {
    box-shadow: #3c4fe0 0 3px 7px inset;
    transform: translateY(2px);
}

/* Media Queries */
@media screen and (max-width: 768px) {
    .container {
        width: 95%;
        padding: 0.5rem;
    }

    .form {
        padding: 1rem;
    }

    .form-control input {
        font-size: 14px;
        padding: 10px 0;
    }

    .login-btn {
        font-size: 14px;
        height: 40px;
    }
}

@media screen and (max-width: 480px) {
    .nav {
        padding: 0 0.5rem;
    }

    .logo {
        height: 3rem;
        width: 30px;
    }

    .form {
        width: 100%;
        padding: 1rem 0.5rem;
    }

    .inputs {
        gap: 0.5rem;
    }

    .form-control {
        margin: 10px 0;
    }

    .login-btn {
        padding: 0 1.5rem;
    }
}

/* Add these styles to your existing login.css */
.buttons-group {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    width: 100%;
    max-width: 200px;
    margin: 0 auto;
}

/* Add these styles to prevent yellow autofill background */
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
    -webkit-box-shadow: 0 0 0 30px rgba(255, 255, 255, 0.1) inset !important;
    -webkit-text-fill-color: white !important;
    transition: background-color 5000s ease-in-out 0s;
    background-clip: content-box !important;
}

/* Add this to your existing login.css */
.register-link {
    position: fixed;
    top: 20px;
    right: 20px;
    background: linear-gradient(135deg, 
        rgba(90, 218, 255, 0.9) 0%, 
        rgba(84, 104, 255, 0.9) 100%
    );
    padding: 10px 20px;
    border-radius: 5px;
    color: white;
    text-decoration: none;
    font-size: 0.9rem;
    transition: all 0.3s ease;
    z-index: 1000;
}

.register-link:hover {
    transform: translateY(-2px);
    box-shadow: 0 5px 15px rgba(84, 104, 255, 0.4);
}

/* Import Google Font */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@600&display=swap');

.login-page {
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(135deg, #1e2024 0%, #17181c 100%);
    position: relative;
    overflow: hidden;
}

.login-container {
    background: rgba(255, 255, 255, 0.95);
    padding: 2rem;
    border-radius: 20px;
    width: 100%;
    max-width: 400px;
    box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
    position: relative;
    z-index: 1;
    backdrop-filter: blur(5px);
}

.login-container h1 {
    font-family: 'Poppins', sans-serif;
    color: #1e2024;
    text-align: center;
    margin-bottom: 2rem;
    font-size: 2.5rem;
    font-weight: 600;
    background: linear-gradient(to right, #5DA7FF, #4D6FFF);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    letter-spacing: 1px;
    position: relative;
}

.login-container h1::after {
    content: '';
    position: absolute;
    bottom: -10px;
    left: 50%;
    transform: translateX(-50%);
    width: 50px;
    height: 3px;
    background: linear-gradient(to right, #5DA7FF, #4D6FFF);
    border-radius: 2px;
}

.input-group {
    margin-bottom: 1.5rem;
}

.input-group input {
    width: 100%;
    padding: 12px;
    background: white;
    border: 1px solid #e1e1e1;
    border-radius: 8px;
    color: #333;
    font-size: 1rem;
    transition: all 0.3s ease;
}

.input-group input:focus {
    outline: none;
    border-color: #5DA7FF;
    box-shadow: 0 0 0 2px rgba(93, 167, 255, 0.2);
}

.input-group input::placeholder {
    color: #999;
}

button {
    background: linear-gradient(to right, #5DA7FF, #4D6FFF);
    color: white;
    padding: 12px;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    font-size: 1rem;
    transition: all 0.3s ease;
    width: 100%;
    margin-top: 1rem;
}

button:hover {
    opacity: 0.9;
    transform: translateY(-1px);
    box-shadow: 0 5px 15px rgba(93, 167, 255, 0.3);
}

.error-message {
    background: rgba(255, 68, 68, 0.1);
    color: #ff4444;
    padding: 10px;
    border-radius: 8px;
    text-align: center;
    margin-bottom: 1rem;
    font-size: 0.9rem;
}

/* Background Animation */
.background-animation {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    z-index: 0;
}

.dot {
    position: absolute;
    width: 10px;
    height: 10px;
    background: rgba(93, 167, 255, 0.15);
    border-radius: 50%;
}

.dot1 { animation: float 6s ease-in-out infinite; top: 20%; left: 20%; }
.dot2 { animation: float 8s ease-in-out infinite; top: 60%; left: 80%; }
.dot3 { animation: float 7s ease-in-out infinite; top: 80%; left: 15%; }
.dot4 { animation: float 9s ease-in-out infinite; top: 30%; left: 90%; }
.dot5 { animation: float 5s ease-in-out infinite; top: 70%; left: 50%; }

@keyframes float {
    0%, 100% { transform: translate(0, 0); }
    50% { transform: translate(15px, -15px); }
}

/* Responsive design */
@media (max-width: 480px) {
    .login-container {
        margin: 1rem;
        padding: 1.5rem;
    }
    
    .login-container h1 {
        font-size: 2rem;
    }
}