/* Import Google Font */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Orbitron:wght@400;500;600;700&display=swap');

/* Add title styling */
.register-container h1 {
    color: white;
    text-align: center;
    margin-bottom: 30px;
    font-size: 2rem;
    letter-spacing: 2px;
    font-weight: 700;
    text-transform: uppercase;
    text-shadow: 0 2px 4px rgba(0, 0, 0, 0.2);
    font-family: 'Orbitron', sans-serif;
}

.register-container h1::after {
    content: '';
    position: absolute;
    bottom: -10px;
    left: 50%;
    transform: translateX(-50%);
    width: 50px;
    height: 3px;
    background: linear-gradient(to right, #5DA7FF, #4D6FFF);
    border-radius: 2px;
}

.register-page {
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #1a1d21;
    position: relative;
    overflow: hidden;
}

/* Add animated background elements */
.register-page::before {
    content: '';
    position: absolute;
    width: 200%;
    height: 200%;
    background: repeating-linear-gradient(
        45deg,
        rgba(93, 167, 255, 0.05) 0%,
        rgba(77, 111, 255, 0.05) 10%,
        transparent 0%,
        transparent 50%
    );
    background-size: 20px 20px;
    animation: moveBackground 20s linear infinite;
}

@keyframes moveBackground {
    0% {
        transform: translate(-50%, -50%) rotate(0deg);
    }
    100% {
        transform: translate(-50%, -50%) rotate(360deg);
    }
}

.register-container {
    background: rgba(30, 32, 36, 0.95);
    padding: 30px;
    border-radius: 20px;
    width: 100%;
    max-width: 400px;
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
    backdrop-filter: blur(10px);
    border: 1px solid rgba(255, 255, 255, 0.1);
    z-index: 1;
}

.register-form {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
}

.form-group input {
    width: 100%;
    padding: 12px;
    background: white;
    border: 1px solid #e1e1e1;
    border-radius: 8px;
    color: #333;
    font-size: 1rem;
    transition: all 0.3s ease;
}

.form-group input:focus {
    outline: none;
    border-color: #5DA7FF;
    box-shadow: 0 0 0 2px rgba(93, 167, 255, 0.2);
}

.register-submit-btn {
    background: linear-gradient(to right, #5DA7FF, #4D6FFF);
    color: white;
    padding: 12px;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    font-size: 1rem;
    transition: all 0.3s ease;
    width: 100%;
    margin-top: 1rem;
}

.register-submit-btn:hover {
    opacity: 0.9;
    transform: translateY(-1px);
    box-shadow: 0 5px 15px rgba(93, 167, 255, 0.3);
}

.error-message {
    background: rgba(255, 87, 87, 0.1);
    color: #ff5757;
    padding: 10px;
    border-radius: 8px;
    text-align: center;
    margin-bottom: 15px;
    font-size: 0.9rem;
    border: 1px solid rgba(255, 87, 87, 0.3);
}

.form-group input::placeholder {
    color: rgba(255, 255, 255, 0.5);
}

/* Responsive design */
@media (max-width: 480px) {
    .register-container {
        margin: 20px;
        padding: 20px;
    }

    .image-preview {
        width: 120px;
        height: 120px;
    }
}

/* Add these styles to your existing Register.css */
.image-upload {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
}

.image-label {
    background: linear-gradient(to right, #5DA7FF, #4D6FFF);
    color: white;
    padding: 10px 20px;
    border-radius: 8px;
    cursor: pointer;
    transition: all 0.3s ease;
}

.image-label:hover {
    opacity: 0.9;
    transform: translateY(-1px);
}

.image-input {
    display: none;
}

.image-preview {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    background-color: rgba(93, 167, 255, 0.1);
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: all 0.3s ease;
    background-size: cover;
    background-position: center;
    border: 3px solid rgba(93, 167, 255, 0.3);
    position: relative;
    overflow: hidden;
}

.image-preview:hover {
    transform: scale(1.02);
    border-color: #5DA7FF;
    box-shadow: 0 0 20px rgba(93, 167, 255, 0.2);
}

.image-preview span {
    color: #5DA7FF;
    font-size: 0.9rem;
    text-align: center;
    padding: 10px;
    background: rgba(0, 0, 0, 0.6);
    border-radius: 8px;
    transition: all 0.3s ease;
}

.image-preview:hover span {
    background: rgba(93, 167, 255, 0.2);
}

/* Input Groups */
.input-group {
    margin-bottom: 20px;
}

.input-group input {
    width: 100%;
    padding: 12px 15px;
    background: rgba(255, 255, 255, 0.05);
    border: 2px solid rgba(93, 167, 255, 0.2);
    border-radius: 10px;
    color: white;
    font-size: 1rem;
    transition: all 0.3s ease;
}

.input-group input:focus {
    outline: none;
    border-color: #5DA7FF;
    background: rgba(93, 167, 255, 0.1);
    box-shadow: 0 0 10px rgba(93, 167, 255, 0.1);
}

.input-group input::placeholder {
    color: rgba(255, 255, 255, 0.5);
}

/* Submit Button */
button[type="submit"] {
    width: 100%;
    padding: 12px;
    background: linear-gradient(to right, #5DA7FF, #4D6FFF);
    border: none;
    border-radius: 10px;
    color: white;
    font-size: 1rem;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
    text-transform: uppercase;
    letter-spacing: 1px;
    margin-top: 10px;
}

button[type="submit"]:hover {
    transform: translateY(-2px);
    box-shadow: 0 5px 15px rgba(93, 167, 255, 0.3);
}

button[type="submit"]:active {
    transform: translateY(1px);
}

/* Background Animation */
.background-animation {
    position: absolute;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.dot {
    position: absolute;
    background: linear-gradient(45deg, #5DA7FF, #4D6FFF);
    border-radius: 50%;
    filter: blur(20px);
    opacity: 0.3;
    animation: float 15s infinite;
}

.dot1 { width: 300px; height: 300px; top: -150px; left: -150px; }
.dot2 { width: 250px; height: 250px; top: 50%; right: -100px; animation-delay: -2s; }
.dot3 { width: 200px; height: 200px; bottom: -100px; left: 30%; animation-delay: -4s; }
.dot4 { width: 180px; height: 180px; top: 20%; left: 70%; animation-delay: -6s; }
.dot5 { width: 160px; height: 160px; bottom: 20%; left: -80px; animation-delay: -8s; }

@keyframes float {
    0%, 100% { transform: translate(0, 0); }
    25% { transform: translate(10%, 10%); }
    50% { transform: translate(-5%, 15%); }
    75% { transform: translate(-15%, -5%); }
}

